___CSS_LOADER_EXPORT___.push([module.id, `* {
    font-family: sans-serif;
    font-family: "Fredoka", sans-serif;
     font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-variation-settings:
    "wdth" 100;
}

h1 {
    font-weight: 600;
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: rgb(75, 75, 75);
    box-sizing: border-box;
}

.main__container {
    background-color: #fff;
    width: 500px;
    padding: 10px 20px;
    border-radius: 20px;
}

.cpf-gerado__container {
    display: flex;
    align-items: center;
    border: 1px solid #acabab;
    border-radius: 10px;
    font-size: 24px;
    height: 50px;
}

.cpf-gerado-text {
    display: block;
    font-size: 20px;
    color: rgb(75, 75, 75);
    margin: 30px 10px 5px 0;
}

.cpf-gerado {
    padding: 5px;
}

#button {
    margin-top: 20px;
    font-size: 24px;
    border-radius: 10px;
    border: 1px solid #000;
    cursor: pointer;

    &:hover {
        transform: scale(1.02);
    }
}

